@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
/* --------------------------General-------------------------- */
body {
  font-family: 'Roboto', sans-serif;
  background-color: #f6f5f6;
  color: white;
  margin: 0px;
  font-weight: 270;
  background-color: #0d3b47;
}

/* --------------------------HEADER------------------------- */
.header {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px 0px 10px 0px;
  height: 120px;
  background-color: #f7c0b2;
}
.logo-holder {
  flex: 1;
}
.name-holder {
  flex: 2;
}
/* -------------------------------------HOME ----------------------------------------------- */
.title-math-options {
  width: 90%;
  margin: auto;
  text-align: center;
  margin-top: 10vh;
  font-size: 24px;
  font-weight: 500;
  color: #f7c0b2;
}
.options-holder {
  margin-top: 10px;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.option {
  width: 100%;
  max-width: 350px;
  margin: 15px 10px 15px 10px;
  padding: 5px;
  height: 200px;
  border-radius: 15px;
}
.img-option {
  width: 100%;
  height: 80%;
  border-radius: 15px 15px 0px 0px;
}
.title-option {
  color: #0d3b47;
  font-weight: 700;
  padding: 5px;
}
.op1 {
  background-color: #d3ffc8;
}
.op2 {
  background-color: #ffeba0;
}
.op3 {
  background-color: #ced7ff;
}
.op4 {
  background-color: #b5f1ff;
}

/* ------------------------------------OTHER PAGES  ------------------------------------*/

.solve-equations-page {
  width: 100%;
}
.simple-header {
  width: 100%;
  margin-bottom: 3vh;
  height: 10vh;
  background-color: #f7c0b2;
  display: flex;
  align-items: center;
  color: #0d3b47;
  font-size: 24px;
  font-weight: 400;
}
.arrow-back-holder {
  margin-left: 2%;
  width: 10%;
}
.arrow-back {
  color: #0d3b47;
}
.page-columns {
  display: flex;
  background-color: #f3f4fa;
  color: #0d3b47;
  width: 90%;
  margin: auto;
}
.column-1 {
  display: flex;
  flex-direction: column;
  width: 40%;
  margin: 5%;
  max-height: 40%;
  overflow: auto;
}
.requirements {
  width: 90%;
}
.req-title {
  font-weight: 600;
}
.method-conditions {
  font-size: 14px;
  max-height: 30vh;
}

.intro {
  font-size: 20px;
  margin-bottom: 3vh;
  text-align: center;
}
.explanation {
  padding: 0% 10%;
  margin: auto;
  text-align: center;
  background-color: #f3f4fa;
  color: #0d3b47;
}

.solve-equations-type-methods-holder {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.solve-equations-type-method {
  max-width: 350px;
  margin: 0px 10px 15px 10px;
  padding: 5px;
  height: 250px;
  border-radius: 15px;
  color: #0d3b47;
  font-size: 18px;
}
.method-op1 {
  background-color: #ced7ff;
}
.method-op2 {
  background-color: #b5f1ff;
}

/* ------------------------------------METHOD PAGE ------------------------------------*/
.method-explanation {
  margin-left: 5%;
  margin-right: 5%;
  text-align: center;
}

/* -----------------------------------FORM -----------------------------------*/
.form-title {
  padding: 10px;
  text-align: center;
  font-weight: 600;
}
.column-2 {
  width: 40%;
  margin: 5%;
  background-color: #f3f4fa;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #0d3b47;
}
.closed-methods-form {
  width: 100%;
}

.form-body {
  display: flex;
  justify-content: space-around;
  height: 200px;
}

.labels {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  color: #0d3b47;
  margin-right: 10px;
}
.inputs {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.input {
  width: 120px;
}
.form-button {
  margin: 10px;
  padding: 4px 8px 4px 8px;
  border-radius: 15px;
  background-color: transparent;
  color: #0d3b47;
  border: solid 2px #0d3b47;
  box-shadow: none;
}
/* -----------------------------------SOLUTIONS-----------------------------------*/
.solutions-section {
  width: 100%;
  max-width: 500px;
}
.section-title {
  padding: 10px;
  text-align: center;
  font-weight: 600;
}
.solutions-holder {
  display: flex;
  justify-content: center;
  width: 100%;
}
.method {
  width: 45%;
  margin: 2.5%;
  background-color: #f7c0b2;
  border-radius: 15px;
}
.method-name {
  min-height: 20%;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
  text-align: center;
  font-weight: 600;
  border-bottom: solid 1px white;
}
.method-answer {
  margin-bottom: 10px;
  padding: 5px;
  text-align: center;
  word-wrap: break-word;
}
/* -----------------------------------GRAPH ---------------------------------------*/
.graph-title {
  padding: 10px;
  text-align: center;
  font-weight: 600;
}
.graph-holder {
  width: 80%;
  margin: auto;
}

.function-plot {
  width: 100% !important;
  text-align: center;
}
.canvas {
  width: 100% !important;
  transform: translate(0, 0) !important;
}

.title {
  width: 100%;
  text-align: center !important;
}
.zoom-and-drag {
  width: 100% !important;
}
.example-sys-eq-holder {
  max-height: 100px;
  width: 90%;
  display: flex;
  justify-content: center;
  margin-top: 1vh;
  margin-bottom: 1vh;
}
.example-sys-eq {
  width: 100%;
}

@media screen and (max-width: 768px) {
  .page-columns {
    flex-direction: column;
  }
  .column-1 {
    width: 90%;
    margin: auto;
    margin-bottom: 15%;
  }
  .column-2 {
    width: 90%;
    margin: auto;
    margin-bottom: 15%;
  }
}
